body {
  font-weight: 300;
  color: #070707;
}

body select {
  border: 1px solid #dde9e9;
}

body a:hover {
  text-decoration: none;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6,
th,
.dropdown-item,
.form-control,
dt {
  font-weight: 300;
}

.text-1 {
  font-size: 1.75rem;
}

.indent-1 {
  margin-left: 1.25rem;
}

.first-line-indent-1 {
  text-indent: 3.5rem;
}

.first-line-outdent-1 {
  margin-left: 1.75rem;
  text-indent: -1.75rem;
}

.text-2 {
  font-size: 1.625rem;
}

.indent-2 {
  margin-left: 2.5rem;
}

.first-line-indent-2 {
  text-indent: 3.25rem;
}

.first-line-outdent-2 {
  margin-left: 1.625rem;
  text-indent: -1.625rem;
}

.text-3 {
  font-size: 1.5rem;
}

.indent-3 {
  margin-left: 3.75rem;
}

.first-line-indent-3 {
  text-indent: 3rem;
}

.first-line-outdent-3 {
  margin-left: 1.5rem;
  text-indent: -1.5rem;
}

.text-4 {
  font-size: 1.375rem;
}

.indent-4 {
  margin-left: 5rem;
}

.first-line-indent-4 {
  text-indent: 2.75rem;
}

.first-line-outdent-4 {
  margin-left: 1.375rem;
  text-indent: -1.375rem;
}

.text-5 {
  font-size: 1.25rem;
}

.indent-5 {
  margin-left: 6.25rem;
}

.first-line-indent-5 {
  text-indent: 2.5rem;
}

.first-line-outdent-5 {
  margin-left: 1.25rem;
  text-indent: -1.25rem;
}

.text-6 {
  font-size: 1.125rem;
}

.indent-6 {
  margin-left: 7.5rem;
}

.first-line-indent-6 {
  text-indent: 2.25rem;
}

.first-line-outdent-6 {
  margin-left: 1.125rem;
  text-indent: -1.125rem;
}

#scroll-to-top {
  color: rgba(91, 192, 222, 0.8);
  font-size: 2rem;
  display: none;
  margin: 10px;
  position: fixed;
  bottom: 75px;
  right: 30px;
  transition: color 0.3s;
  z-index: 99;
}

#scroll-to-top:hover {
  cursor: pointer;
  color: #5bc0de;
}

#scroll-to-top:active {
  color: rgba(91, 192, 222, 0.8);
}

#header {
  margin: 4.5rem 0 0 0;
}

#info {
  padding: 0.75rem 0;
}

#intro {
  background-color: rgba(165, 222, 228, 0.2);
}

#about {
  background-color: rgba(165, 222, 228, 0.2);
}

#map {
  width: 100%;
  height: 60vh;
}

#footer {
  color: #ffffff;
  background-color: #9f353a;
  margin: 6rem 0 0 0;
  padding: 2rem 0 6.375rem 0;
}

#warning {
  padding: 0.5rem;
  text-align: center;
  position: fixed;
  z-index: 99;
  left: 0;
  bottom: 0;
  width: 100%;
  color: #ffffff;
  background-color: rgba(15, 37, 64, 0.8);
}

#warning-image {
  height: 50px;
}

.container {
  max-width: 1330px;
}

.box .box-logo {
  width: 100%;
  margin: 0 0 0.875rem 0;
}

.box .box-info {
  padding: 0.75rem 0 0 0;
}

.box .box-search .box-search-icon {
  color: #ffffff;
}

.box .box-search .box-search-keyword {
  padding: 0.5rem 0.75rem 0 0.75rem;
}

.box .box-search .box-search-keyword a:hover {
  text-decoration: underline;
}

.box .box-item a {
  color: #337ab7;
}

.box .box-item a:hover {
  color: #0b346e;
}

.box .box-item .box-bar {
  color: #ffffff;
  padding: 0.25rem 0.75rem;
  border-radius: 0.25rem;
  background-color: #9f353a;
}

.box .box-item .box-bar a {
  color: #ffffff;
}

.box .box-item .box-bar a:hover {
  color: #bdc0ba;
}

.box .box-item .box-item-image {
  margin: 0 0 1rem 0;
  padding: 1.5rem 0 1.5rem 0;
  border: 2px solid #dde9e9;
  border-radius: 0.25rem;
}

.box .box-item .box-item-image-size {
  width: 100px;
}

.box .box-item .box-item-content {
  padding: 0.875rem 0.25rem 0.875rem 0.25rem;
  margin: 0 0 1rem 0;
  border-bottom: 1px solid #bdc0ba;
}

.box .box-item .box-item-content .entry-price {
  color: #d9534f;
  font-size: 1.375rem;
}

.box .box-item-pagination {
  margin: 1rem 0;
}

.btn-variety {
  font-size: 1.25rem;
  color: #ffffff;
  margin: 0 0.375rem 0.375rem 0;
  padding: 0 0.625rem;
  background-color: rgba(240, 173, 78, 0.9);
}

.btn-variety:hover {
  color: #ffffff;
}

.btn-tag {
  font-size: 1.25rem;
  color: #ffffff;
  margin: 0 0.375rem 0.375rem 0;
  padding: 0 0.625rem;
  background-color: rgba(91, 192, 222, 0.9);
}

.btn-tag:hover {
  color: #ffffff;
}

.breadcrumb {
  font-size: 1.25rem;
  background-color: #fcfdfd;
  margin: 0;
  padding: 0;
}

.breadcrumb .breadcrumb-item.active {
  color: #070707;
}

.pagination .page-link {
  color: #070707;
  background-color: #ffffff;
  border: 1px solid #dee2e6;
}

.pagination .page-link:hover {
  color: #dee2e6;
  background-color: #9f353a;
  border-color: #9f353a;
}

.pagination .page-item.active .page-link {
  color: #dee2e6;
  background-color: #9f353a;
  border-color: #9f353a;
}

.pagination .page-item.disabled .page-link {
  background-color: #ffffff;
  border-color: #dee2e6;
}

.card .card-header {
  background-color: rgba(165, 222, 228, 0.2);
}

.card .card-image {
  border: 2px solid #dde9e9;
  border-radius: 0.25rem;
}

.card .card-fooder {
  background-color: rgba(165, 222, 228, 0.2);
}

.ui-autocomplete {
  padding: 0.25rem 0;
  -webkit-border-radius: 0.25rem;
  -moz-border-radius: 0.25rem;
  border-radius: 0.25rem;
  max-height: 300px;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 99 !important;
}

.ui-tooltip {
  color: #FFF;
  padding: 0 1rem;
  background: rgba(189, 192, 186, 0.8);
  border: 1px solid #FFF;
  box-shadow: none;
}

@media (max-width: 575px) {
  .sm-display {
    display: none;
  }
}

@media (max-width: 767px) {
  .md-display {
    display: none;
  }
}

@media (max-width: 991px) {
  .lg-display {
    display: none;
  }
}

@media (max-width: 1199px) {
  .xl-display {
    display: none;
  }
}

@media (max-width: 575.98px) {
  #warning {
    font-size: 1rem;
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  #warning {
    font-size: 1.25rem;
  }
}

@media (min-width: 768px) and (max-width: 991.98px) {
  #warning {
    font-size: 1.5rem;
    letter-spacing: 1.25rem;
  }
}

@media (min-width: 992px) and (max-width: 1199.98px) {
  #warning {
    font-size: 1.5rem;
    letter-spacing: 1.5rem;
  }
}

@media (min-width: 1200px) {
  #warning {
    font-size: 1.75rem;
    letter-spacing: 1.75rem;
  }
}




